  --primaryFont:'Rock Salt', cursive;
  --secondaryFont:'Josefin Sans', sans-serif;
}

.box {
  width: 50vw;  
  margin: 10% auto;
  position: relative;
  display: contents;
}

#card-container {
  position: relative;
  margin: 30px;
  width: 25rem;
  height: 25rem;
  z-index: 1;
  float: left;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

