:root {
  --highlight: #ffb20f;
  --background: #f9f5ff;
  --main-colour:#01463c;
  --contrast-colour:  #af1b3f;
  --main-marg: 100px;
  --primaryFont:'Rock Salt', cursive;
  --secondaryFont:'Josefin Sans', sans-serif;
}

html {
  scroll-behavior: smooth;
}


h1.myh1 {
  color: #01463c;
  color: var(--main-colour);
  font-family: 'Rock Salt', cursive;
  font-family: var(--primaryFont);
  font-size: 3.5rem;
  padding: 100px;
  padding: var(--main-marg);
  text-align: center;
}
#myh1 {
  color: #af1b3f;
  color: var(--contrast-colour);
  font-family: 'Rock Salt', cursive;
  font-family: var(--primaryFont);
  font-size: 3.5rem;
  padding: 100px;
  padding: var(--main-marg);
  text-align: center;
  width: -webkit-fill-available;
}

h2  {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  text-align: center;
}
/* #intro  {
  font-family: var(--secondaryFont);
  text-align: left;
} */

h3 {
  font-size: 2vw;
  padding: 50px 0px;
}

h4 {
  font-family: 'Rock Salt', cursive;
  font-family: var(--primaryFont);
  font-size: 6rem;
}

.navigation {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  font-size: 2rem;
  padding: 50px 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  color: #01463c;
  color: var(--main-colour);
  text-decoration: none;
}

.navigation:hover{
  color: #ffb20f;
  color: var(--highlight);
}

.navigation-active{
  color: #af1b3f;
  color: var(--contrast-colour); 
  font-family: 'Josefin Sans', sans-serif; 
  font-family: var(--secondaryFont);
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  padding: 1.5rem;
  text-decoration: none;
}
.navigation-active:hover{
  color: #ffb20f;
  color: var(--highlight);
}

.toggle{
  background-color: #af1b3f;
  background-color: var(--contrast-colour);
}

li {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  font-size: 1rem;
  padding: 0.5rem;
}

p {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  padding: 10px 0px;
  font-size: 1.5rem;
  text-align: center;
  color: #01463c;
  color: var(--main-colour);
}


.hero{
  display:flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 1rem;

}


.blob{
  background-image: url(/caoimhejyoti-react/static/media/blob_1_green.ed19fb99.png);
  background-repeat: round;
}
.eyes{
  background-image: url(/caoimhejyoti-react/static/media/eyes-large.96479738.png);
  background-size: cover;
  background-position: center 40%;
}

.footer {
  position: "absolute";
  bottom: 0;
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  font-size: 1.5rem;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content:space-between; 
  flex-wrap: wrap;
  background-color: #01463c;
  background-color: var(--main-colour);
  color: #f9f5ff;
  color: var(--background);
}

.footer-img{
  height: 3rem;
  display: flex;
}
.footer-icon{
  height: 4rem;
  display: flex;
}
.btn-img{
  height: 5rem;
  display: flex;
  justify-content: center;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100vh;
  position: relative;
  line-height: 1;
  font-family: sans-serif;

}

